export default {
	data() {
		return {
			istheme:true,
			show: false,
			content: '您已经成功参与抢单，该订单需要平台工作人员进行人工审核；审核通过后请到“订单”列表查看具体订单信息并且联系客户预约上门进行服务',
			height:"",
			background:{
				backgroundImage: "linear-gradient(90deg, #54C3F1, #00ABEB)",
			},
			data:{},
			isactive:false,
			id:0, // 使用 marker点击事件 需要填写id
			title: 'map',
			scale:10,
			latitude: 39.909,
			longitude: 116.39742,
			covers: [{
				latitude: 39.909,
				longitude: 116.39742,
				iconPath: '../../static/wait-list/location.png',
				width:30,
				height:30,
				
			}, {
				latitude: 39.90,
				longitude: 116.39,
				iconPath: '../../static/wait-list/location1.png',
				width:30,
				height:30,
			}]
		}

	},
	
	onLoad(event) {
		// TODO 后面把参数名替换成 payload
		const payload = event.detailDate;
		
		// 目前在某些平台参数会被主动 decode，暂时这样处理。
		try {
			this.data= JSON.parse(decodeURIComponent(payload));
			console.log(this.data);
			let loction = this.data.longitude.split(",");
			console.log(loction);
			this.latitude = loction[1];
			this.longitude  =loction[0];
			this.covers[0].latitude = loction[1];
			this.covers[0].longitude  =loction[0];
			this.covers[1].longitude=uni.getStorageSync('longitude');
			this.covers[1].latitude=uni.getStorageSync('latitude');
		} catch (error) {
			this.data = JSON.parse(payload);
			console.log(this.data);
		};
		
	},
	computed:{
		dataDoortime(){
			if(this.data){
				console.log(this.data);
				return  this.data.door_time.substring(5,this.data.door_time.length-3)
			}
		}
	},
	methods: {
		navlociton(){
			let that  = this;
			console.log(that.latitude);
			wx.openLocation({
				latitude:Number(that.latitude) ,
				longitude: Number(that.longitude) ,
				success() {
					console.log("成功");
				}
			})
		},
		opentake(){
			let that =this;
			let phone = uni.getStorageSync('phone');
			let user_uid = uni.getStorageSync('uid');
			let badgecont = uni.getStorageSync("badgecont");
			let code = this.data.code;
			console.log("phone"+phone);
			console.log(user_uid);
			if (phone) {
					uni.request({
						url:this.$apiUrl+"work/take",
						method:"POST",
						dataType:JSON,
						data:{
							uid:user_uid,
							code:code,//订单编号
							WorkNautica:[this.longitude,this.latitude],
							phone:phone
						},
						success(res) {
							console.log(res);
							// console.log(res);
							const data = JSON.parse(res.data);
							console.log(data.code);
							switch (data.code){
								case 200:
									//抢单成功体醒
									//提醒订单
									that.show = true;
									that.isactive = true;
									let badgecont = 0;
									// that.$badge++;
									badgecont ++;
									// console.log(this.$badge);
									uni.$emit('updatebadgecont',{badgecont:badgecont});
									setTimeout(()=>{
										uni.navigateBack({
										    delta: 1
										});
									},3000);
									break;
								case 300:
									uni.showToast({
										title:data.msg
									});
									that.isactive = true;
									break;
								case 400:
									uni.showToast({
										title:data.msg
									})
									break;	
								default:
									uni.showToast({
										title:data.mag
									})
									break;
							}
							
						},
						fail() {
							console.log(res);
						}
					})
			} else {
				uni.showModal({
				    title: '提示',
				    content: '请先完成师傅服务类型注册，在抢单',
				    success: function (res) {
				        if (res.confirm) {
				            console.log('用户点击确定');
							//如果没有手机说明用户没有注册跳转
							uni.navigateTo({
								url:"../settlement/settlement"
							})
				        } else if (res.cancel) {
				            console.log('用户点击取消');
							
				        }
				    }
				});
			}
		}
	},
	//自定义分享页面
	onShareAppMessage(e){
		return {
			title: this.$overShare.title,
			path: this.$overShare.path,
			imageUrl:this.$overShare.imageUrl,
			
		}
	}
}
